import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { CartItem, addItem } from "../../redux/cart/cartSlice";
import { selectCartItemById } from "../../redux/cart/cartSelectors";

type PizzaBlockProps = {
  id: string;
  title: string;
  imageUrl: string;
  price: number;
  sizes: number[];
  types: number[];
};

const PizzaBlock: React.FC<PizzaBlockProps> = ({
  id,
  title,
  imageUrl,
  price,
  sizes,
  types,
}) => {
  const dispatch = useDispatch();
  const typeNames = ["—Ç–æ–Ω–∫–µ", "—Ç—Ä–∞–¥–∏—Ü—ñ–π–Ω–µ"];
  const findItem = useSelector(selectCartItemById(id));
  const addedCount = findItem ? findItem.count : 0;

  const [activeType, setActiveType] = useState<number>(0);
  const [activeSize, setActiveSize] = useState<number>(0);

  const onClickAdd = () => {
    const item: CartItem = {
      id,
      title,
      imageUrl,
      price,
      type: typeNames[activeType],
      size: sizes[activeSize],
      count: 0,
    };
    dispatch(addItem(item));
  };

  return (
    <div className="pizza-block-wrapper">
      <div className="pizza-block">
        <img
          width="260"
          height="260"
          className="pizza-block__image"
          src={imageUrl}
          alt="Pizza"
        />
        <h4 className="pizza-block__title">{title}</h4>
        <div className="pizza-block__selector">
          <ul>
            {types.map((typeId, index) => (
              <li
                key={index}
                onClick={() => {
                  setActiveType(typeId);
                }}
                className={activeType === index ? "active" : ""}
              >
                {typeNames[typeId]}
              </li>
            ))}
          </ul>
          <ul>
            {sizes.map((size, index) => (
              <li
                key={index}
                onClick={() => {
                  setActiveSize(index);
                }}
                className={activeSize === index ? "active" : ""}
              >
                {size} —Å–º.
              </li>
            ))}
          </ul>
        </div>
        <div className="pizza-block__bottom">
          <div className="pizza-block__price">–≤—ñ–¥ {price} ‚Ç¥</div>
          <button
            onClick={onClickAdd}
            type="button"
            className="button button--outline button--add"
          >
            <svg
              width="12"
              height="12"
              viewBox="0 0 12 12"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                fill="white"
              />
            </svg>
            <span>–î–æ–¥–∞—Ç–∏</span>
            {addedCount > 0 && <i>{addedCount}</i>}
          </button>
        </div>
      </div>
    </div>
  );
};
export default PizzaBlock;
