import { useEffect, useRef, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { selectSort } from "../../redux/filters/filtersSelectors";
import { setSortValue } from "../../redux/filters/filtersSlice";

import { SortType } from "../../redux/pizzas/pizzasOperations";

type SortItem = {
  name: string;
  sortProperty: "rating" | "title" | "price";
  order: "desc" | "asc";
};

const sortValues: SortItem[] = [
  {
    name: "–ø–æ–ø—É–ª—è—Ä–Ω—ñ—Å—Ç—é (–∑–∞ –∑–º–µ–Ω—à–µ–Ω–Ω—è–º)",
    sortProperty: "rating",
    order: "desc",
  },
  {
    name: "–ø–æ–ø—É–ª—è—Ä–Ω—ñ—Å—Ç—é (–∑–∞ –∑–±—ñ–ª—å—à–µ–Ω–Ω—è–º)",
    sortProperty: "rating",
    order: "asc",
  },
  {
    name: "—Ü—ñ–Ω–æ—é (–∑–∞ –∑–º–µ–Ω—à–µ–Ω–Ω—è–º)",
    sortProperty: "price",
    order: "desc",
  },
  {
    name: "—Ü—ñ–Ω–æ—é (–∑–∞ –∑–±—ñ–ª—å—à–µ–Ω–Ω—è–º)",
    sortProperty: "price",
    order: "asc",
  },
  {
    name: "–∞–ª—Ñ–∞–≤—ñ—Ç–æ–º (–≤—ñ–¥ –ê –¥–æ –Ø)",
    sortProperty: "title",
    order: "asc",
  },
  {
    name: "–∞–ª—Ñ–∞–≤—ñ—Ç–æ–º (–≤—ñ–¥ –Ø –¥–æ –ê)",
    sortProperty: "title",
    order: "desc",
  },
];

const Sort: React.FC = () => {
  const [isOpen, setIsOpen] = useState(false);
  const sortRef = useRef<HTMLDivElement>(null);

  const dispatch = useDispatch();

  const sortValue = useSelector(selectSort);

  useEffect(() => {
    const onClickOutside = (e: MouseEvent) => {
      const target = e.target as HTMLElement;
      if (target.offsetParent !== sortRef.current) {
        setIsOpen(false);
      }
    };

    document.addEventListener("click", onClickOutside);

    return () => {
      document.removeEventListener("click", onClickOutside);
    };
  }, []);

  const onSelectValue = (value: SortType) => {
    dispatch(setSortValue(value));
    setIsOpen(false);
  };

  return (
    <div ref={sortRef} className="sort">
      <div className="sort__label">
        <div>
          <svg
            width="10"
            height="6"
            viewBox="0 0 10 6"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
              fill="#2C2C2C"
            />
          </svg>
          <b>–°–æ—Ä—Ç—É–≤–∞–Ω–Ω—è –∑–∞:</b>
        </div>

        <span
          onClick={() => {
            setIsOpen((prev) => !prev);
          }}
        >
          {sortValue.name}
        </span>
      </div>
      {isOpen && (
        <div className="sort__popup">
          <ul>
            {sortValues.map((obj, index) => (
              <li
                onClick={() => {
                  onSelectValue(obj);
                }}
                key={index}
                className={
                  obj.sortProperty === sortValue.sortProperty &&
                  obj.order === sortValue.order
                    ? "active"
                    : ""
                }
              >
                {obj.name}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};
export default Sort;
